*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Playfair Display', sans-serif;
}
.navbar1 {
  /* background: rgba(0, 0, 0, 0.137); */
  background: "transparent";
  height: 80px;
  display: flex;
  width: 100vw;
  justify-content: center;
  font-weight: 700;
  position: fixed;
  transition: -webkit-transform 1000ms;
  transition: transform 1000ms;
  transition: transform 1000ms, -webkit-transform 1000ms;
  z-index: 10;
}

.navbar1.move {
  -webkit-transform: translateY(-200px);
          transform: translateY(-200px);
}
.desktop1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.desktop1 img {
  width: 75%;
  margin: auto;
  padding: 20px;
}
.navlink {
  color: #fff;
  font-size: 2rem;
  display: flex;
  align-items: center;
  text-decoration: none;
  cursor: pointer;
}

@media screen and (max-width: 530px) {
  .navlink {
    position: absolute;
    top: 10px;
    left: 25px;
  }

  .desktop img {
    width: 75%;
    margin: auto;
    padding: 20px;
  }
}

.navicon {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
  color: #fff;
  -webkit-transform: translate(100%, 50%);
          transform: translate(100%, 50%);
  font-weight: bold;
  font-size: 2rem;
}

.container1 {
  background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.8),
      rgba(0, 0, 0, 0.329)
    ),
    url(/static/media/homepage.6f69af9b.jpeg);
  height: 100vh;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

.content {
  height: calc(100vh - 80px);
  max-height: 100%;
  /* padding: 0rem calc(100vw/2); */
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.contentItems {
  /* background: #f76d2844; */
  display: flex;
  width: clamp(100%, 650, 100%);
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  margin-left: 50px;
  margin-top: 100px;
  align-items: flex-start;
  padding: 1rem 3rem;
  width: 700px;
  color: white;
}
.contentItems p {
  font-size: 24px;
}
@media screen and (max-width: 650px) {
  .container {
    background: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.8),
        rgba(0, 0, 0, 0.2)
      ),
      url(/static/media/homepage.6f69af9b.jpeg);
    height: 100vh;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
  }
  .contentItems {
    width: 100%;
  }
}
.navHead {
  font-size: clamp(1rem, 3rem, 5rem);
  margin-bottom: 2rem;
  letter-spacing: 3px;
  line-height: 1;
}

p {
  /* font-size: clamp(2rem, 2.5vw, 3rem); */
  font-size: clamp(1rem, 1.2rem, 1.4rem);
  /* font-size: clamp(0.5rem, 1.2rem, 2rem); */
  /* font-size: 1rem; */
  margin-bottom: 2rem;
  line-height: 1.3;
  font-family: "Open Sans", sans-serif;
}

.btn1 {
  font-size: 1.8rem;
  padding: 1rem 2rem;
  border: solid 5px #f76d28;
  color: white;
  transition: 0.2s ease-out;
  margin-bottom: 10px;
  border-radius: 5px;
}

.btn1:hover {
  background: #f76d28;
  transition: 0.2s ease-out;
  cursor: pointer;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.sideContainer {
  position: fixed;
  z-index: 100;
  width: 350px;
  height: 100%;
  background: #201414;
  display: grid;
  justify-content: center;
  align-items: center;
  top: 0;
  transition: left 0.3s ease-in-out;
  left: -500px;
}

.sideContainer.show {
  left: 0;
}
@media screen and (max-width: 400px) {
  .sideContainer {
    width: 100%;
  }
}

/* export const CloseIcon = styled(FaTimes){
	color: #000;
} */

.close {
  position: absolute;
  top: 1.2rem;
  right: 1.5rem;
  background: transparent;
  border: transparent;
  font-size: 1.4rem;
  cursor: pointer;
  outline: none;
  color: #fdfdfd;
  transition: -webkit-transform 0.5s ease-in;
  transition: transform 0.5s ease-in;
  transition: transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
}
.close:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.sideMenu {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 80px);
  text-align: center;
  margin-bottom: 5rem;
}

@media screen and (max-width: 480px) {
  .sideMenu {
    grid-template-rows: repeat(4, 60px);
  }
}

.sideLink {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  text-decoration: none;
  list-style: none;
  transition: 0.2s ease-in-out;
  color: #fdfdfd;
  cursor: pointer;
}

.sideLink:hover {
  color: #f76d28;
  transition: 0.2s ease-in-out;
}

.btnWrap {
  display: flex;
  justify-content: center;
  margin-top: 3rem;
}

.sideRoute {
  background: #f76d28;
  white-space: nowrap;
  padding: 16px 64px;
  color: #fff;
  font-size: 16px;
  outline: none;
  border: none;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  text-decoration: none;
}

.sideRoute:hover {
  transition: -webkit-transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.favContainer {
  /* background: #fdfdfd; */
  background: linear-gradient(to top, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.2)),
    url(/static/media/homepage1.b1bcf0b2.jpeg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  color: #fff;
  text-align: center;
  padding: 1rem 1rem;
}

.favContent {
  height: calc(100vh - 80px);
  height: 100%;
  /* padding: 0rem calc(100vw/2); */
  display: flex;
  grid-gap: 30px;
  gap: 30px;
  justify-content: center;
  align-items: center;
}

.favContainer h2 {
  font-size: clamp(1rem, 3rem, 4rem);
  margin: 1rem 0;
  z-index: 10;
}

.favContainer h3 {
  font-size: clamp(1rem, 2vw, 3rem);
  margin: 1rem 0;
}

.favContainer p {
  margin-bottom: 1rem;
  width: 45vw;
  font-size: clamp(0.7rem, 1.2rem, 1.3rem);
  /* margin-bottom: 30%; 
    margin-left: 60px;  */
  width: 400px;
  text-align: right;
}
.textContent {
  /* height: 100px; */
  padding: 10px;
  background: #f76d2850;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 500px;
  width: 100%;
  margin-top: 20px;
}
.favImage {
  /* background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.2)), url('../images/restaurant.jpg'); */
  /* background: url('../images/favourite.jpg'); */
  /* height: 300px; */
  max-width: 300px;
  width: 100%;
  height: auto;
  /* margin-left: 30px; */
  box-shadow: 3px 5px #ffc500;
  background-position: center;
  background-size: cover;
  margin-top: 20px;
}

@media screen and (max-width: 935px) {
  .favContent {
    flex-direction: column;
  }
  .textContent p {
    width: 90%;
    font-size: 24px;
    padding: 10px;
    text-align: center;
  }
  .favContainer h2 {
    font-size: 36px;
    margin: 1rem 0;
    z-index: 10;
  }
}

.favBtn {
  font-size: 1rem;
  padding: 1rem 4rem;
  border: none;
  width: 250px;
  align-self: center;
  background: #f76d28;
  color: #fff;
  transition: 0.2 ease-out;
  margin-top: 30px;
}
.favBtn:hover {
  background: #5203c3;

  transition: 0.2s ease-out;
  cursor: pointer;
}

