  align-items: flex-start;
  min-height: 70px;
  max-width: 100vw;
}

.footerColumn {
  padding: 16px 24px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-size: 1rem;
  grid-gap: 5px;
  gap: 5px;
}

.footerColumn div {
  margin-top: 5px;
}

.footerColumn a {
  text-decoration: none;
  color: white;
}

